import React from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCoffee } from '@fortawesome/free-solid-svg-icons'

class Dummy extends React.Component {
    render() {
        console.log(this.props)
        return <button
            name="menuOpen"
            onClick={(e) => this.props.menuClick(e)}
            className="menuOpenButton">
            <FontAwesomeIcon icon={faCoffee} onClick={this.props.menuClick} />
            Click menuClick
        </button>
    }
}

export default class Writings extends React.Component {
    menuClick = (e) => {
        console.log(e.target.name)
    }
    render() {
        return <Dummy menuClick={this.menuClick} />
    }
}